import { Login, WXLogin } from '@/static/request/api_login.js';
import { getMe } from '@/static/request/api_userInfo.js';
export default {
	data() {
		return {
			phone: '',
			password: '',
			isPassword: true // 是否展示密码
		};
	},
	methods: {
		loginSuccess(data) {
			getApp().globalData.gUserInfo = data
			console.log(data);
			uni.reLaunch({
				url: '../app',
				success: () => {
					this.gToastSuccess('登录成功')
				}
			})
			this.gLoading(this, false);
		},
		login() {
			if (this.phone === '') this.gToastError('手机号不能为空');
			else {
				this.gLoading(this, true);
				Login({
					phone: this.phone,
					password: this.password
				})
				.then(res => {
					uni.setStorageSync('token', res.data.token)
					this.loginSuccess(res.data.personalUserInfo)
				})
				.catch(err => {
					this.gLoading(this, false)
				})
			}
		},
		wxLogin() {
			this.gLoading(this, true);
			uni.getUserInfo({
				provider: 'weixin',
				withCredentials: true,
				lang: 'zh_CN',
				success: res => {
					// 获取code，请求openid
					uni.login({
						provider: 'weixin',
						success: loginRes => {
							// 调用wx登录接口，获取token
							WXLogin({
								code: loginRes.code,
								avatarUrl: res.userInfo.avatarUrl,
								nickname: res.userInfo.nickName
							})
							.then(res => {
								uni.setStorageSync('token', res.data.token);
								this.loginSuccess(res.data.personalUserInfo);
							})
							.catch(err => {
								this.gLoading(this, false);
							});
						}
					});
				}
			});
		}
	},
	mounted() {
		/* 检查是否有存储token，验证登录身份 */
		if (uni.getStorageSync('token')) {
			getMe()
			.then(res => {
				this.loginSuccess(res.data)
			})
		}
	}
}
